body {
    text-align: center;
    background: var(--tg-theme-bg-color);
    color: var(--tg-theme-text-color);
}

th, td {
    padding: 15px;
}

table, th, td {
    text-align: center;
    color: var(--tg-theme-text-color);
    border: 1px solid blue;
    border-collapse: collapse;
}